@import url(https://fonts.googleapis.com/css?family=Orbitron:900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(322deg, rgba(2,0,36,1) 38%, rgba(19,162,191,1) 100%);
  background: linear-gradient(128deg, rgba(2,0,36,1) 38%, rgba(19,162,191,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1 {
  font-family: 'Orbitron', sans-serif;
  color: rgb(46, 200, 231);
}
